.Block{
    width: 70%;
    margin:auto;
    position: relative;
    margin-top:35px;
    display: flex;
    justify-content: space-between;
}


.Navbar{
    list-style: none;
    text-align: bottom;
    display:inline-flex;
    justify-content: space-between;
    margin:0;
    padding: 0;
    width:30%;
    
    
}

.Header{
    font-size: 2.2em;
    font-weight: 700;
    display: inline-block;
    width:35%;
    transition: font-size 0.3s;
}
.Tab{
    display: inline-block;
}

.Tab:nth-child(2){
    
    margin-left:30px;
}

.Tab a{
    font-size: 1.4em;
    font-weight: 300;
    text-decoration: none;
    color:black;
    transition: font-size 0.3s;
    
}


.Tab a:hover, .Header:hover{
    color:#4384c5;
    cursor:pointer;
}

.Selected{
    padding-bottom: 5px;
    border-bottom-color: black;
    border-bottom-style: solid;
    border-bottom-width: 2px;
}

.Toggle{
    display: none;
}

.Vertical{
    position: absolute;
    display: block;
    top:45px;
    list-style: none;
    margin:0;
    padding: 0;
    transition: height 0.3s;
}

.Vertical li{
    margin:0;
    padding:0;
    width:15em;
    margin-top: 20px;
}

.Vertical a{
    font-size: 1.1em;
    font-weight: 300;
    text-decoration: none;
    color:black;
    transition: font-size 0.3s;
    
}


.Vertical a:hover{
    color:#4384c5;
    cursor:pointer;
}

.Disabled{
    height: 0;
    overflow: hidden;
}






@media (max-width:1250px){
    .Block{
        margin-top:30px;
    }
    
    
    .Header{
        font-size: 1.7em;
        font-weight: 600;
        display: inline-block;
        width:35%;
    }
    .Tab:nth-child(2){
        
        margin-left:25px;
    }
    
    .Tab a{
        font-size: 1.2em;
        font-weight: 300;
        text-decoration: none;
        color:black;
        
    }
    
}

@media (max-width:1050px){
    .Toggle{
        display: inline-block;
        width:24px;
        height:20px;
        position: relative;
    }

    .Toggle span::before,.Toggle span::after, .Toggle span {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 100%;
        height: 3px;
        -webkit-transition: all 0.1s;
        -moz-transition: all 0.1s;
        -ms-transition: all 0.1s;
        -o-transition: all 0.1s;
        transition: all 0.1s;
        background: #2A2F36;
    }

    .Toggle span::before{
        top: 8px;
    }
    .Toggle span::after{
        top: 16px;
    }

    .Block{
        min-width: 660px;
    }

    .Navbar{
        display: none;
    }      
    
}

