.Block{
    width: 65%;
    margin:auto;
    position: relative;
    margin-top:55px;
    margin-bottom: 100px;
    text-align: left;
    font-family: "Muli",sans-serif;
    min-width: 560px;
    
}

.Divider{
    height:0;
    padding:1px;
    background-color: black;
}


.Section h1{
    font-size: 28px;
    margin:15px;
    margin-left:0;
    margin-top:25px;
    color:#202020;
    transition: font-size 0.3s;
}

.Section h2{
    font-size: 17px;
    margin:2px;
    color:#202020;
    transition: font-size 0.3s;
}

.Section h3{
    font-size: 15px;
    margin:0;
    color:#202020;
    transition: font-size 0.3s;
}

.Inline{
    display: flex;
    justify-content: space-between;
}

.Block p{
    color:#5f614a;
    margin:10px;
    margin-left:0;
    font-size: 15px;
    transition: font-size 0.3s;
}

.Special span{
    color:#5f614a;
    margin:15px;
    margin-left:0;
    font-size: 22px;
    transition: font-size 0.3s;
}



.ImageSection img{
    position: relative;
    width:90%;
    margin:auto;
    margin-top: 25px;
    margin-bottom: 25px;
    height: auto;
}

@media (max-width:1250px){
    
    
    .Section h1{
        font-size: 26px;
        margin:13px;
        margin-left: 0;
        margin-top:24px;
    }
    
    .Section h2{
        font-size: 16px;
        margin:2px;
        margin-left: 0;
    }
    
    .Section h3{
        font-size: 14px;
        margin:0;
    }
    
    .Block p{
        font-size: 14px;
    }
    
    .Special span{
        font-size: 21px;
    }
    
    
    
    .ImageSection img{
        position: relative;
        width:90%;
        margin:auto;
        margin-top: 25px;
        margin-bottom: 25px;
        height: auto;
    }
}

