.Block{
    margin-top: 30px;
    margin-bottom: 60px;
    position: relative;
    border-bottom-color: rgb(121, 121, 121,0.6);
    border-bottom-style: solid;
    border-bottom-width: 2px;
    padding-bottom: 15px;
}

.Block:hover{
    cursor: pointer;
}

.Block:hover div:last-child{
    background-color:rgb(110, 106, 106);
    opacity: 0.4;
    transition: opacity 0.4s;
}

.Block a{
   text-decoration: none;
}

.Block img{
    width: 100%;
    height: auto;
}

.Float{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    text-align: center;
    opacity:0;
    transition: opacity 0.4s;
    z-index:300

}

.Intro{
    font-size: 22px;
    font-style:italic;
    color:rgb(121, 121, 121) 
}


.SlideImage{
    position:relative;
    height:300px;
}
.SlideImage img:nth-child(1){
    width: 100%;
    height: auto;
    position:absolute;
    animation-name: fade;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    z-index: 2;
}
.SlideImage img:nth-child(2){
    width: 100%;
    height: auto;
    position:absolute;
    animation-name: fade;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-delay: 5s;
    z-index:1;
}
.SlideImage img:nth-child(3){
    width: 100%;
    height: auto;
    position:absolute;
    animation-name: fade;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-delay: 10s;
    z-index: 0;
}

@keyframes fade{
  0%   {opacity: 1; z-index:200;}
  16%  {opacity: 1;z-index:200}
  33% {opacity: 0; z-index:200;}
  34% {z-index:0;}
  67% {z-index:0;opacity:0}
  68% {opacity: 1;z-index:100}
  100%{opacity: 1;z-index:100}
}